const SheetData = ({ selectedSheet, data: rawData, compareSheetData, name }) => {
  const types = useMemo(() => [
    { title: '3年', value: 3 },
    { title: '7年', value: 7 },
  ].concat(name === 'flash' ? [
    { title: '總表', value: 'portfolio' },
    { title: '大總表', value: 'compact' },
  ] : [
    { title: '3年總表', value: 'portfolio' },
    { title: '7年總表', value: 'portfolio-7' },
  ]), [name])

  const notes = useMemo(() => {
    if (name === 'flash' || name === 'mcp') return ['Sample', 'MP']
    return ['ES', 'CS', 'MP']
  }, [name])
  const [type, setType] = useState(3)
  const [downloading, setDownloading] = useState()
  const { startYear, yearControl, startQuarter, quaterControl } = useYearControl()
  const [page, setPage] = useState(0)
  // const year = getYear(new Date())
  // const quater = getQuarter(new Date())
  const handleData = useCallback(srcData => {
    if (!srcData) return null
    let sd = srcData
    if (!sd[0][0]) sd = srcData.slice(1)
    const appearInSeven = sd[0].findIndex(d => d === '7yrs')
    const appearInPortfolio = sd[1].findIndex(d => type === 'portfolio' ? d === '3yrs' : d === '7yrs')
    if (type === 7) return sd.filter(d => d[appearInSeven] !== 'FALSE')
    if (type === 'portfolio' || type === 'portfolio-7') return sd.filter(d => d[appearInPortfolio] !== 'FALSE')
    return sd
  }, [type])
  const data = handleData(rawData)
  const headerRow = useTableHeader(data)
  const doubleHeader = useMemo(() => data && data[0].some(h => !h), [data])
  const tableData = useMemo(() => data ? data.slice(doubleHeader ? 2 : 1) : [], [data, doubleHeader])
  const pages = useMemo(() => {
    return tableData.reduce((all, row) => {
      let i = all.length - 1
      if (row[0]) {
        i = all.push([]) - 1
      }
      all[i]?.push(row)
      return all
    }, [])
  }, [tableData])
  const headerKeys = useTableHeader(data)
  const compareData = useMemo(() => compareSheetData ? handleData(compareSheetData).slice(doubleHeader ? 2 : 1) : [], [handleData, compareSheetData, doubleHeader])
  const compareKeyIndex = useMemo(() => data?.[0].findIndex(h => /^product/i.test(h)), [data])
  const [compareMark, comparePages] = useCompareData(compareData, compareKeyIndex)
  return (
    <Tabs style={{ marginTop: '2em' }}>
      <TabList>
        <Tab>簡報產生器</Tab>
        <Tab disabled={!compareData.length}>更新對照表</Tab>
      </TabList>
      <TabPanel>
        <Flex my="1em" alignItems="center">
          {yearControl}
          {quaterControl}
          {selectedSheet && types.map((t, i) => name !== 'mcp' && (
            <Button.Outline
              active={type === t.value}
              mx="0.25em"
              onClick={() => setType(t.value)}
              key={i}
            >{t.title}</Button.Outline>
          ))}
        </Flex>
        {typeof type === 'number' && pages && pages[page] && (
          <>
            <Pagination
              pageCount={pages.length}
              pageRangeDisplayed={5}
              marginPagesDisplayed={2}
              onPageChange={({ selected }) => setPage(selected)}
            />
            <PageSimulator name={`${name}-${type}y-${page + 1}`}>
              <DramDataTable
                headerKeys={headerKeys}
                allData={data}
                compareMark={compareMark}
                compareKeyIndex={compareKeyIndex}
                data={pages[page]}
                doubleHeader={doubleHeader}
                quater={startQuarter}
                year={startYear}
                type={type}
                notes={notes}
                name={name}
              />
            </PageSimulator>
            <Box>
              <Button onClick={() => setDownloading(true)} disabled={downloading}>
                批次下載{downloading ? '中...' : ` (${pages.length}頁)`}
              </Button>
              {downloading && (
                <DramDownloader
                  headerKeys={headerKeys}
                  allData={data}
                  compareMark={compareMark}
                  compareKeyIndex={compareKeyIndex}
                  pages={pages}
                  doubleHeader={doubleHeader}
                  quater={startQuarter}
                  year={startYear}
                  type={type}
                  notes={notes}
                  name={name}
                  onDownloaded={() => setDownloading(false)}
                />
              )}
            </Box>
            {/* <PageControl
              key={selectedSheet}
              allData={tableData}
              headerRow={headerRow}
              page={page}
              data={pages[page]}
              compareMark={compareMark}
              sheetName={selectedSheet}
              startYear={year}
            /> */}
          </>
        )}
        {/^portfolio/.test(type) && data && (
          <PortfolioPage
            data={data}
            name={name}
            headerKeys={headerKeys}
            filterKey={`Portfolio.${name === 'flash' ? 'Sub' : (type === 'portfolio-7' ? '7yrs' : '3yrs')}`}
          />
        )}
        {type === 'compact' && data && (
          <PortfolioPage data={data} name={name} headerKeys={headerKeys} filterKey="Portfolio.All" variant="compact" />
        )}
      </TabPanel>
      <TabPanel>
        <CompareSummary
          keyIndex={compareKeyIndex}
          name={name}
          data={pages}
          headerRow={headerRow}
          compareMark={compareMark}
          sheetName={selectedSheet}
          comparePages={comparePages}
        />
      </TabPanel>
    </Tabs>
  )
}

const specialName = {
  mcp: 'flash',
}

const DramPage = ({ name = 'dram' }) => {
  return (
    <Container>
      <Link as={GatsbyLink} to={`/${specialName[name] || name}`}>
        {`< Home`}
      </Link>
      <SheetLoader name={name}>
        <SheetData />
      </SheetLoader>
    </Container>
  )
}

export default DramPage
