import React, { useMemo, useRef, useEffect, useState, useCallback } from 'react';
import styled from 'styled-components'
import isObject from 'lodash/isObject'
import set from 'lodash/set';
import isNumber from 'lodash/isNumber';
import compact from 'lodash/compact';
import concat from 'lodash/concat';
import floor from 'lodash/floor';
import range from 'lodash/range';

import Box from '../components/Box'
import Flex from '../components/Flex'
import Text from '../components/Text';
import parseDate from 'utils/parseDate';
import PageTitle from './PageTitle';
import DramTimeline from './DramTimeline';

const StyledTable = styled.table`
  table-layout: fixed;
  width: 100%;
  th,
  td {
    white-space: pre;
    padding: 0.5rem;
    border: 3px solid white;
    line-height: 1.25;
  }
`
let group = []
const replaceSup = (t) => String(t)
  .replace(/\{([^{]+)\}/g, (...d) => `<sup>${d[1]}</sup>`)
  .replace(/\{\{([^}]+)\}\}/g, (m, g) => `<span style="color: #D6262F">${g}</span>`)

const noteBg = { mp: '#A4CCDB', cs: '#4D9185', es: '#DC888A', sample: '#2F6EBA' }
// const bodybg = ['#B1B1B2', '#CACACA', '#E8E8E8']
const noteFunc = (noteArr) => {
  const groups = noteArr.reduce((g, n) => {
    if (n.startsWith('-')) {
      let lastGroup = g[g.length - 1]
      if (!lastGroup) {
        g.push([''])
        lastGroup = g[g.length - 1]
      }
      lastGroup[1] = lastGroup[1] || []
      lastGroup[1].push(n.substring(1))
    } else {
      g.push([n])
    }
    return g
  }, [])
  return (
    <Box as="ul" pl="1em">
      {groups.map((g, i) => (
        <li key={i}>
          <span dangerouslySetInnerHTML={{ __html: replaceSup(g[0]) }} />
          {g[1] && noteFunc(g[1])}
        </li>
      ))}
    </Box>
  )
}

const DramDataTable = ({
  allData,
  data,
  notes,
  quater,
  year,
  type,
  headerKeys,
  name,
  scale,
  doubleHeader,
  compareMark,
  compareKeyIndex,
}) => {
  const [nmGroup, setNmGroup] = useState([]);
  const tableRef = useRef()
  const timelineData = useMemo(() => {
    const dateGetter = notes.map(key => headerKeys.find(d => d.Header === key))
    if (dateGetter.some(d => !d)) return []
    const timeline = data.map(d => dateGetter.map(({ columns }) => columns.map(({ accessor }, i) => {
      const dStr = accessor(d)
      return parseDate(dStr, i)
    })))
    return timeline
  }, [data, headerKeys, notes])
  const timeIsInRange = useMemo(() => {
    if (!Number.isNaN(type)) {
      const dateStart = year + (quater - 1) / 4
      const dateEnd = year + type * 1 + (quater - 1) / 4
      const inRange = (d, isEnd) => {
        if (!d) return true
        if (typeof d == 'string') return false
        const y = d.year + d.month / 12
        return isEnd ? y >= dateStart : y <= dateEnd
      }
      return timelineData.map(d => d.some(([s, e]) => inRange(s) && inRange(e, true)))
    }
    // const dateEnd = new Date(year, (quater - 1) * 3, 1)
  }, [timelineData, quater, year, type])
  const okTimeline = useMemo(() => timelineData.filter((d, i) => timeIsInRange[i]), [timelineData, timeIsInRange])
  const yearRow = useMemo(() => range(type * (type < 7 ? 4 : 2)).map((n) => {
    if (!n) {
      return year
    }
    if (((type < 7 ? quater : 1) + n) % (type < 7 ? 4 : 2) === 1) {
      return year + floor(((type < 7 ? quater : 1) + n) / (type < 7 ? 4 : 2))
    } else {
      return ''
    }
  }), [type, quater, year])
  const getIndex = useCallback(pttn => allData?.[0]?.findIndex(d => pttn.test(d)), [allData])
  const roadmapheaderRow = useMemo(() => allData && isNumber(type) && name === 'mcp' ? (
    [
      allData[0][getIndex(/Package\s|\nBall/i)],
      allData[0][getIndex(/Product/i)],
      allData[0][getIndex(/SLC\s|\nNAND/i)],
      '',
      allData[0][getIndex(/LPDDR2/i)],
      '',
      ...yearRow
    ]
  ) : (
    concat(
      name === 'flash' ? compact([
        data?.[0][getIndex(/flash/i)] && allData[0][getIndex(/flash/i)],
        data?.[0][getIndex(/S-NOR/i)] && allData[0][getIndex(/S-NOR/i)],
        data?.[0][getIndex(/S-NAND/i)] && allData[0][getIndex(/S-NAND/i)],
        data?.[0][getIndex(/^Density/i)] && allData[0][getIndex(/^Density/i)],
      ]) : [allData?.[0][getIndex(/^Density/i)]], [allData?.[0][getIndex(/product/i)]],
      name === 'flash' ? (data?.[0][getIndex(/Description/i)] ? 'Description' : []) : 'Description',
      [...yearRow]
    )
  )
    , [allData, type, data, name, yearRow, getIndex])
  const headerRow = useMemo(() => {
    if (doubleHeader) {
      let groupMemo = []
      // 空白代表要group ['a', 'b', '', 'c']
      // b後面的空白要跟b group一起，所以先反轉陣列，從後面檢查回來，只要是空白，就等到下一個值出現然後把他們group起來
      return roadmapheaderRow.slice(0).reverse().reduce((all, h, i) => {
        // 檢查有沒有值
        if (h) {
          // 如果年份只有一季，但還是要有第二列
          if (isNumber(h) && !groupMemo.length) {
            all.push({
              Header: h,
              columns: [{
                Header: type < 7 ? (i ? 'Q4' : 'Q1') : (i ? 'H2' : 'H1'),
                id: `${h}-${1}`,
              }],
            })
            return all
          }
          // 檢查有沒有之前先標記要變成group的
          if (groupMemo.length) {
            // if true 把現在這一格也加入group
            if (isNumber(h)) {
              groupMemo.push(i)
              all.push({
                Header: h,
                columns: groupMemo.slice().reverse().map((j, i, { length }) => ({
                  Header: type < 7 ? `Q${(4 - length) + i + 1}` : `H${i + 1}`,
                  // accessor: d => d[colLength - j - 1],
                  id: `${h}-${i + 1}`,
                })),
              })
            }
            if (!isNumber(h) && name === 'mcp') {
              groupMemo.push(i)
              all.push({
                Header: h,
                columns: groupMemo.map((j, i) => ({
                  Header: i ? 'I/O' : 'Density',
                  // accessor: d => d[colLength - j - 1],
                  id: `${h}-${i + 1}`,
                })),
              })
            }
            // 清空group的標記
            groupMemo = []
          } else {
            // if false 就是一般的header
            all.push({ Header: h, id: i })
          }
        } else {
          // 沒有值，之後要跟別人group，先暫存起來
          groupMemo.push(i)
        }
        return all
      }, [])
        .reverse() // 最後記得再反轉回來
    }
    return []
  }, [doubleHeader, type, roadmapheaderRow, name])
  const numberIndex = useMemo(() => headerRow.reduce((res, d, i) => {
    if (!res && isNumber(d.Header)) {
      res = i
    }
    return res
  }, 0), [headerRow])
  const productIndex = useMemo(() => headerRow.findIndex(d => /product/i.test(d.Header)), [headerRow])
  const headerWidth = useMemo(() => name === 'mcp' ? ['7rem', '12rem', '12rem', '12rem'] : numberIndex > 3 ? ['6.9rem', '6.9rem', '10.4rem', '13.8rem'] : numberIndex > 2 ? ['9rem', '12.75rem', '16.25rem'] : ['15rem', '23rem'], [numberIndex, name])
  const noteText = useMemo(() => {
    if (!allData) return []
    const noteIndex = allData[0].findIndex(d => (type > 3 && name == 'dram') ? /7yrs\s|\nNote/i.test(d) : /^note/i.test(d))
    if (noteIndex < 0) return []
    if (!data) return []
    return (data?.[0][noteIndex].split('\n') || '')
  }, [allData, type, name, data])
  const feature = useMemo(() => {
    if (!allData) return []
    const featureIndex = allData[0].findIndex(d => /feature/i.test(d))
    const feature7yrIndex = allData[0].findIndex(d => /^7feature/i.test(d))
    if (featureIndex < 0 || (name === 'dram' && type > 3 && feature7yrIndex < 0)) return []
    if (!data) return []
    return (data?.[0][(name === 'dram' && type > 3) ? feature7yrIndex : featureIndex].split('\n') || '')
  }, [allData, data])
  const bodyData = useMemo(() => allData && data?.reduce((res, d, i) => {
    if (!timeIsInRange[i]) return res
    const dataToCompare = compareMark?.[d[compareKeyIndex]]
    const getDataByHeaderPttn = pttn => {
      const index = getIndex(pttn)
      const v = d[index]
      const isDiff = dataToCompare && dataToCompare[index] !== v
      return isDiff ? `{{${v}}}` : v
    }
    if (name == 'mcp') {
      res.push([
        getDataByHeaderPttn(/Package\s|\nBall/i),
        getDataByHeaderPttn(/Product/i),
        getDataByHeaderPttn(/SLC\s|\nNAND/i),
        getDataByHeaderPttn(/SLC\s|\nNAND/i, 1),
        getDataByHeaderPttn(/LPDDR2/i),
        getDataByHeaderPttn(/LPDDR2/i, 1),
        { text: '', col: type > 3 ? 14 : 12, }
      ])
      return res
    }
    res.push(concat((name === 'flash' ? concat(
      data[0][getIndex(/flash/i)] ? getDataByHeaderPttn(/flash/i) : [],
      data[0][getIndex(/S-NOR/i)] ? getDataByHeaderPttn(/S-NOR/i) : [],
      data[0][getIndex(/S-NAND/i)] ? getDataByHeaderPttn(/S-NAND/i) : [],
      data[0][getIndex(/^Density/i)] ? getDataByHeaderPttn(/^Density/i) : []
    ) : [getDataByHeaderPttn(/^Density/i)]), [
      getDataByHeaderPttn(/Product/i),
      (name !== 'flash' ? `${getDataByHeaderPttn(/I\/O\s|\nWidth/i) + ' ' + getDataByHeaderPttn(/Voltage/i) + `\n` + getDataByHeaderPttn(/Speed/i)}` : (
        getDataByHeaderPttn(/Description/i)
      )),
      {
        text: name !== 'flash' ? [
          getDataByHeaderPttn(/^Technology\s|\nNode/i),
          getDataByHeaderPttn(/^Process\s|\nNode/i),
          getDataByHeaderPttn(/Package/i)
        ].filter(Boolean).join(', ') : '',
        col: type > 3 ? 14 : 12,
        nrnd: getDataByHeaderPttn(/NRND/i) === 'TRUE',
        nm: name === 'flash' && getDataByHeaderPttn(/Technology/i),
      },
    ]))
    return res
  }, []), [allData, type, data, name, getIndex, compareMark, compareKeyIndex, year, quater])
  const columnIndex = useMemo(() => headerRow.reduce((res, d, i) => {
    if (!res && d.columns) {
      res = i
    }
    return res
  }, 0), [headerRow])
  const columnCount = useMemo(() => headerRow.reduce((res, d, i) => {
    if (d.columns && i < numberIndex) {
      res = res + d.columns.length
    }
    return res
  }, 0), [headerRow, numberIndex])
  const bodybg = useMemo(() => range(Boolean(numberIndex - columnIndex) ? (numberIndex - columnIndex + columnCount) : numberIndex).map(num => num == productIndex ? '#CACACA' : num < productIndex ? '#B1B1B2' : '#E8E8E8'), [productIndex, numberIndex, columnIndex])

  const rowSpansfuc = (key) => bodyData?.map(d => d[key]).reverse().reduce((res, h, i, { length }) => {
    if (h) {
      if (group.length) {
        set(res, [length - i - 1], group.length + 1)
        group = []
      } else {
        set(res, [length - i - 1], 1)
      }
    } else {
      group.push(i)
    }
    return res
  }, {})
  // console.log(bodybg)
  useEffect(() => {
    if (tableRef.current) {
      const masterBox = tableRef.current.getBoundingClientRect()
      const cells = tableRef.current.querySelectorAll('.timeline-cell[data-nm')
      if (cells.length) {
        const cellGroup = []
        cells.forEach(cell => {
          const nm = cell.dataset.nm
          const boundBox = cell.getBoundingClientRect()
          const boxCenter = (boundBox.top + boundBox.height / 2 - masterBox.top) / scale
          let currentGroup = cellGroup[cellGroup.length - 1]
          if (nm !== currentGroup?.nm) {
            currentGroup = { nm, left: (boundBox.right - masterBox.left) / scale }
            cellGroup.push(currentGroup)
          }
          currentGroup.top = Math.min(currentGroup.top || boxCenter, boxCenter)
          currentGroup.bottom = Math.max(currentGroup.bottom || boxCenter, boxCenter)
        })
        setNmGroup(cellGroup)
      }
    }
  }, [bodyData])
  // console.log(bodyData)
  return (
    <Box position="relative" pl="3.75em" pr="1em" width="1920px" height="1080px" ref={tableRef}>
      {nmGroup.length > 0 && (
        <Box.FullAbsolute>
          {nmGroup.map(({ nm, top, bottom, left }, i) => (
            <Flex
              key={nm + i}
              position="absolute"
              style={{ top, height: bottom - top, left }}
              border="1px solid"
              borderLeft="none"
              alignItems="center"
            >
              <Box
                style={{
                  writingMode: 'vertical-rl',
                  textRrientation: 'mixed',
                }}
                transform="translateX(120%)"
              >
                {nm}
              </Box>
            </Flex>
          ))}
        </Box.FullAbsolute>
      )}
      <PageTitle>{data[0][0]}</PageTitle>
      <Flex pt="7em">
        <StyledTable flex={1} style={{ position: "relative", background: 'linear-gradient(0deg, rgba(227,227,227,0.85), rgba(238,238,238,0.3))' }}>
          <thead style={{ fontSize: '1.25rem', fontWeight: '900' }}>
            <tr>
              {headerRow.map(({ Header, columns }, i) => (
                <Box
                  as="th"
                  style={{ borderRight: i == (numberIndex - 1) && '10px solid' }}
                  height="44px"
                  width={headerWidth[i]}
                  fontSize={name == 'mcp' && !i && '14px'}
                  color="white"
                  bg={isNumber(Header) ? '#C63B38' : '#234B69'}
                  rowSpan={!columns && 2}
                  colSpan={columns?.length || 1} key={i}>{Header}
                </Box>
              ))}
            </tr>
            <tr>
              {headerRow.map(({ columns }, k) => columns?.map(({ Header }, i, { length }) => (
                <Box
                  as="th"
                  fontWeight="bold"
                  style={{
                    borderRight: ((numberIndex - columnIndex) && k == (numberIndex - 1) && i == (length - 1)) && '10px solid white',
                    whiteSpace: name === 'mcp' && 'nowrap',
                  }}
                  height="44px"
                  bg="#CACACA"
                  width="68px"
                  key={i}
                >{Header}</Box>
              )))}
            </tr>
          </thead>
          {/* Apply the table body props */}
          <tbody style={{ fontSize: '19px', height: '23em' }}>
            {bodyData.map((body, i) => (
              <Box as="tr" key={i}>
                {body.map((d, k) => d && (isObject(d) ? (
                  <Box
                    as="td"
                    colSpan={d.col}
                    // style={{ background: 'linear-gradient(0deg, rgba(227,227,227,0.85), rgba(238,238,238,0.3))' }}
                    key={k}
                    position="relative"
                    overflow="visible"
                    className="timeline-cell"
                    data-nm={d.nm || undefined}
                    style={rowSpansfuc(0)[i] ? { borderBottom: 'none', borderLeft: '10px solid white' } : { borderTop: 'none', borderBottom: 'none', borderLeft: '10px solid white' }}
                  >
                    <DramTimeline
                      data={okTimeline[i]}
                      startYear={year}
                      startQuarter={type === 3 ? quater : 1}
                      yearSpan={type}
                      colors={notes.map(n => noteBg[n.toLocaleLowerCase()])}
                    />
                    <Box position="relative" pl="8em">
                      <span
                        dangerouslySetInnerHTML={{ __html: replaceSup(d.text) }}
                      />
                      {d.nrnd && <Text.Inline fontWeight="bold" color="#234b69" pl="1em">*NRND</Text.Inline>}
                    </Box>
                  </Box>
                ) : (
                  <Box
                    as="td"
                    fontWeight={!k && 'bold'}
                    bg={bodybg[k]}
                    rowSpan={rowSpansfuc(k)[i]}
                    style={{
                      whiteSpace: name === 'mcp' && k > 0 && 'nowrap',
                    }}
                    // style={{ borderRight: (k - (headerRow.length - body.length)) == (numberIndex - 1) && '10px solid white'}}
                    key={k}
                  >
                    <Flex
                      minHeight="2.25rem"
                      alignItems="center"
                      justifyContent={k === headerRow.findIndex(d => /description/i.test(d.Header)) ? 'flex-start' : 'center'}
                      textAlign={k === headerRow.findIndex(d => /description/i.test(d.Header)) ? 'flex-start' : 'center'}
                      px={name === 'mcp' ? '0' : "1em"}
                    >
                      <Box dangerouslySetInnerHTML={{ __html: replaceSup(d) }} />
                    </Flex>
                  </Box>
                )))}
              </Box>
            ))}
          </tbody>
        </StyledTable>
        <Flex flexDirection="column" ml={name == 'mcp' ? '2em' : "3.5em"} width="22.375em">
          <Box border="1.5px solid transparent" bg="transparent" p="0.5rem">　</Box>
          <Flex mb="1em">
            {notes.map((d, i) => (
              <Box fontSize="1.25rem" width="100%" fontWeight="bold" textAlign="center" bg={noteBg[d.toLocaleLowerCase()]} color="white" p="0.375rem 0.5rem" key={i}>{d}</Box>
            ))}
          </Flex>
          {feature[0] && (
            <>
              <Box bg="#9FA0A0" fontSize="1.125rem" textAlign="center" color="white" p="0.5rem" fontWeight="bold">FEATURE</Box>
              <Box p="0.5rem" style={{ background: 'linear-gradient(0deg, rgba(227,227,227,0.85), rgba(238,238,238,0.3))' }}>
                {feature[0] && noteFunc(feature)}
              </Box>
            </>
          )}
          <Box bg="#9FA0A0" fontSize="1.125rem" textAlign="center" color="white" p="0.5rem" fontWeight="bold">NOTE</Box>
          <Box flex={1} p="0.5rem" style={{ background: 'linear-gradient(0deg, rgba(227,227,227,0.85), rgba(238,238,238,0.3))' }}>
            {noteText[0] && noteFunc(noteText)}
          </Box>
        </Flex>
      </Flex>
    </Box>
  )
};

export default DramDataTable;
